.mainDiv {
    display: flex;
    padding-bottom: 100px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.header {
    font-size: 30px;
    background: linear-gradient(89.97deg, #AE67FA 25%, #F49867 75%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.description p {
    color: #81AFDD;
    font-size: 25px;
    font-weight: 350;
    line-height: normal;

    margin-top: 1.5rem;
    max-width: 700px;
}

.image {
    flex: 1;
    opacity: 0.6;
    height: 500px;
}

.image2 {
    display: none;
}

.headerText {
    flex-direction: column;
    margin-left: 1rem;
}

@media screen and (min-width: 2500px) {
    .header {
        font-size: 35px;
    }

    .description p {
        color: #81AFDD;
        font-size: 33px;
        font-weight: 350;
        line-height: normal;

        margin-top: 1.5rem;
        margin-left: 5rem;
        max-width: 800px;
    }
}



@media screen and (max-width: 700px) {
    .mainDiv {
        flex-direction: column;
        padding-bottom: 300px;
    }

    .header {
        margin-right: 0;
        font-size: 27px;
    }

    .description p {

        margin-top: 1.5rem;
        margin-left: 0;
        max-width: 600px;
    }
    .image {
        display: none;
        height: 400px;
    }

    .image2 {
        flex: 1;
        opacity: 0.6;
        height: 400px;
        display: flex
    }
}
